@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@200;400&family=Merriweather+Sans:ital@1&family=Merriweather:ital,wght@0,300;1,300;1,400&family=Oxygen:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@700&family=Montserrat:wght@500&family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  color: #17202a;
  padding: 0;
}
.App {
  width: 100%;
  max-width: 100%;
}

